<template>
  <div class="picdetail text-break ">
       <div class="i1">
            <img  :src="computedImage(personalData)">
            <div class="rightbox ">
                <strong>{{personalData.姓名}}</strong>
                <strong>{{personalData.身份}}</strong> 
                <strong>Email: <a href="mailto:#">{{personalData["E-mail:"]}}</a></strong>
                <strong v-html="personalData.others"></strong>
            </div>
       </div>
        <strong>{{Object.keys(personalData)[21]}}</strong>
        <span v-html="personalData[Object.keys(personalData)[21]]"></span>
        <strong>Research Interests</strong>
        <span>{{personalData["研究方向（Research Interests）"]}}</span>
        <strong>Education</strong>
        <span v-html="personalData.教育经历"></span>
        <strong>Work experience</strong>
        <span v-html="personalData.工作经历"></span>
        <h3 class="titlefont">Academic achievements</h3>
        <strong>Major research projects</strong>
        <span v-html="personalData.项目"></span>
        <strong>Publications</strong>
        <span v-html="personalData.论著"  v-highlight="personalname"></span>
        <strong>Honors &amp; Awards</strong>
        <span v-html="personalData.荣誉与奖励"></span>
        <strong>Conference Presentations/Panels</strong>
        <span v-html="personalData.会议"></span>
        <strong>{{Object.keys(personalData)[22]}}</strong>
        <span v-html="personalData[Object.keys(personalData)[22]]"></span>

  </div>
</template>

<script>
export default {
data(){
    return{
        personalData:{},
        personalname:[],
        // personalname:["Cohen, J. B.","Cohen J. B.","Cohen J.B.","Tiwari, Pravash","Pravash Tiwari","Feng Y","王涵","Wang Han","Ling, Xiaolu",],
        
    }
},
methods:{
    computedImage(item) {
        if (item.photo == 1) {
          return item.logo;
        } else {
          return '../../static/piclogo/默认头像 .svg';
        }
     },
  //转换英文名称缩写
   abbreviateName(fullName) {
      let nameParts = fullName.split(' ');
      nameParts = nameParts.map((namePart, idx) => {
        return idx === 0 ? namePart : namePart.charAt(0).toUpperCase() ;
      });
      return nameParts.join(',');
    },
    hightname(){
       if(this.personalData.姓名 == 'Cohen Jason Blake'){
           this.personalname = ["Cohen, J. B.","Cohen J. B.","Cohen J.B."];
       }if(this.personalData.姓名 == 'Pravash Tiwari'){
         this.personalname = ["Tiwari, Pravash","Pravash Tiwari"];
       }if(this.personalData.姓名 == '王涵'){
         this.personalname = ["王涵","Wang Han"];
       }if(this.personalData.姓名 == 'Xiaolu Ling'){
         this.personalname = ["Ling, Xiaolu"];
       }if(this.personalData.姓名 == '王硕'){
         this.personalname = ["Wang, S"];
       }
     
    }
},
mounted(){
    this.personalData = this.$route.query ;
    this.hightname();
},
}
</script>

<style scoped>
.picdetail{
  display: grid; 
  grid-template-columns: 1fr; 
  grid-template-rows: repeat(auto-fill, minmax(180px, 1fr)); 
  gap: 1em 0px; 
  justify-content: center; 
  align-content: center; 
  justify-items: stretch; 
  align-items: center; 
  margin-left: 10em;
  margin-right: auto;
  padding-left: 1em;
  white-space: pre-line;
  line-height: 2em;
}
.i1{
  display: grid; 
  grid-template-columns: 200px 1fr; 
  column-gap: 2em;
  justify-items: start; 
  align-items: center; 
}
.picdetail img { 
    width: 100%;
    }
.picdetail strong{
  font-size: clamp(1rem, 4vw, 2rem);
  font-weight: 800;
}
.i1 .rightbox{
    margin-left: 5em;
    margin-top: 2em;
    display: grid;
    grid-template-columns:1fr;
    gap: 2em;
}
.titlefont{
  font-weight: 800;
  color: #2e6a86;
}
@media screen and (max-width: 780px) {
  .picdetail{
  margin-left: 2em;
}
  .i1{
  grid-template-columns: 1fr;
  justify-items: start; 
  align-items: stretch; 
}
 .i1 .rightbox{
    margin-left:0;
    margin-top: 2em;
    display: grid;
    grid-template-columns:1fr;
    gap: 2em;
}
.picdetail img { 
    width: 50%;
  }
}
</style>


